<template>
    <div>
        <v-toolbar color="primary" app
            style="color: #E8E8E8"
        >
            <v-btn icon
                @click.stop="drawer = !drawer"
            >
                <v-icon
                    color="#E8E8E8"
                >list</v-icon>
            </v-btn>

            <v-toolbar-title v-if="page === 'dashboard'">
                {{ user.property }} Smart Farm Dashboard
            </v-toolbar-title>

            <v-toolbar-title v-if="page === 'user'">
                {{ currentUser.displayName }}
            </v-toolbar-title>

            <v-spacer></v-spacer>

            <v-tooltip bottom>
                <v-btn icon v-if="page === 'dashboard'"
                    slot="activator"
                    class="mt-1 mr-2"
                    v-scroll-to="'#alerts'"
                    @click="openAlerts"
                >
                    <v-badge right :color="GET_ALERTS ? 'error' : alertsList.length === 0 ? 'accent' :'success'">
                        <v-icon
                            slot="badge"
                            small
                            v-if="GET_ALERTS"
                        >
                            fas fa-exclamation
                        </v-icon>
                            <v-icon v-else-if="alertsList.length === 0"
                                slot="badge"
                                small
                            >
                                fas fa-bell-slash
                            
                            </v-icon>
                            <v-icon v-else
                                slot="badge"

                            >
                                fas fa-check
                            </v-icon>
                            <span>
                                <v-icon color="white">
                                    fas fa-bells
                                </v-icon>
                            </span>
                    </v-badge>

                </v-btn>
                <span>Alerts</span>
            </v-tooltip>

            <v-tooltip bottom>
                <v-btn icon v-if="page === 'dashboard'"
                    slot="activator"
                    class="mt-1 ml-3 mr-2"
                    v-scroll-to="'#system'"
                    @click="openSystem"
                >
                    <v-badge right color="success">
                        <v-icon
                            slot="badge"

                        >
                            fas fa-check
                        </v-icon>
                        <span>
                            <v-icon color="white">
                                fas fa-cogs
                            </v-icon>
                        </span>
                    </v-badge>

                </v-btn>
                <span>System</span>
            </v-tooltip>

            <v-tooltip bottom>
                <v-btn icon v-if="page === 'dashboard'"
                    to="user/devices" slot="activator"
                >
                    <v-icon color="#E8E8E8"
                        class="mt-1 ml-3"
                    >
                        fas fa-tablet-rugged
                    </v-icon>
                </v-btn>
                <span>Devices</span>
            </v-tooltip>

            <v-btn icon>
                <v-icon class="mr-0 mt-1"
                    color="#E8E8E8"
                >
                    fas fa-calendar
                </v-icon>
            </v-btn>

            <v-toolbar-title
                style="font-size: 12pt;"
                class="ml-0 mt-2 mr-2"
            >
                {{ day }}
            </v-toolbar-title>

            <v-btn icon>
                <v-icon class="mr-0 mt-1"
                    color="#E8E8E8"
                >
                    fas fa-clock
                </v-icon>
            </v-btn>

            <v-toolbar-title
                style="font-size: 12pt;"
                class="ml-0 mt-2 mr-2"
            >
                {{ now }}
            </v-toolbar-title>
            </v-toolbar>
        <v-navigation-drawer
            v-model="drawer"
            temporary
            app
        >

            <v-list class="pa-1">
                <v-list-tile avatar>
                <v-list-tile-avatar>
                    <img :src="currentUser.photoURL">
                </v-list-tile-avatar>

                <v-list-tile-content>
                    <v-list-tile-title>
                    {{ currentUser.displayName }}
                    </v-list-tile-title>
                </v-list-tile-content>
                </v-list-tile>
            </v-list>

            <v-list class="pt-0" dense>
                <v-divider></v-divider>

                <v-list-tile
                    v-for="opt in options"
                    :key="opt.type"
                    :to="opt.link"
                >

                <v-list-tile-action>
                    <v-icon color="primary">{{ opt.icon }}</v-icon>
                </v-list-tile-action>

                <v-list-tile-content>
                    <v-list-tile-title>{{ opt.type }}</v-list-tile-title>
                </v-list-tile-content>
                </v-list-tile>

                <v-list-tile v-if="page === 'user'"
                    to="/dashboard"
                >

                    <v-list-tile-action>
                        <v-icon color="primary">fas fa-home</v-icon>
                    </v-list-tile-action>

                    <v-list-tile-content>
                        <v-list-tile-title>Back to Dashboard</v-list-tile-title>
                    </v-list-tile-content>
                </v-list-tile>


                <v-list-tile @click="logout()"
                >

                    <v-list-tile-action>
                        <v-icon color="primary">fas fa-sign-out-alt</v-icon>
                    </v-list-tile-action>

                    <v-list-tile-content>
                        <v-list-tile-title>Sign Out</v-list-tile-title>
                    </v-list-tile-content>
                </v-list-tile>

            </v-list>
        </v-navigation-drawer>
    </div>
</template>

<script>
import { mapState, mapGetters, mapActions, mapMutations } from 'vuex';
import firebase from 'firebase';

export default {
  name: 'Toolbar',
  props: {
    page: String,
  },
  computed: {
    ...mapState([
      'user',
      'alertsList',
    ]),
    ...mapGetters([
      'GET_DARK',
      'GET_ALERTS'
    ]),

    currentUser () {
        return firebase.auth().currentUser;
    }
  },
  data: () => ({
    options: [
      { type: 'My Profile', icon: 'fas fa-user-circle', link: '/user/profile' },
      { type: 'Devices', icon: 'fas fa-tablet-rugged', link: '/user/devices' },
      { type: 'Dashboard Settings', icon: 'fas fa-grip-horizontal', link: '/user/dashboard_settings' },
      { type: 'Property Settings', icon: 'fas fa-image', link: '/user/property' },
      { type: 'Privacy & Access Settings', icon: 'fas fa-lock-alt', link: '/user/privacy' },
      { type: 'Feedback', icon: 'fas fa-comment-alt-dots', link: '/user/feedback' },
    ],

    drawer: false,
    now: new Date(),
    day: new Date(),
  }),

  created() {
    setInterval(() => this.now = (new Date()).toLocaleTimeString('en-AU'));

    setInterval(() => this.day = (new Date()).toLocaleDateString('en-AU'));
  },

  methods: {
    ...mapActions([
      'DARK_MODE',
    ]),

    ...mapMutations({
       openAlerts: 'TOGGLE_ALERTS',
       openSystem: 'TOGGLE_SYSTEM'
    }),

    logout() {
        firebase.auth().signOut().then(() => {
            this.$router.replace('/');
        });
    }
  },
};

</script>
